<template>
  <li :class="classes" @click.stop="select" @mouseout.stop="blur" v-show="!hidden"><slot>{{ showLabel }}</slot></li>
</template>

<script>
import Emitter from '../../mixins/emitter'

const prefixCls = `x-select-item`

export default {
  name: 'XOption',

  componentName: 'select-item',

  mixins: [ Emitter ],

  props: {
    value: {
      type: [String, Number],
      required: true
    },
    label: {
      type: [String, Number]
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      selected: false,
      index: 0,
      isFocus: false,
      hidden: false,
      searchLabel: ''
    }
  },

  computed: {
    classes () {
      return [
        `${prefixCls}`,
        {
          [`${prefixCls}--disabled`]: this.disabled,
          [`${prefixCls}--selected`]: this.selected,
          [`${prefixCls}--focus`]: this.isFocus
        }
      ]
    },
    showLabel () {
      return (this.label) ? this.label : this.value
    }
  },

  methods: {
    select () {
      if (this.disabled) {
        return false
      }

      this.dispatch('XSelect', 'select-option', this.value)
    },

    blur () {
      this.isFocus = false
    },

    queryChange (val) {
      const parsedQuery = val.replace(/(\^|\(|\)|\[|\]|\$|\*|\+|\.|\?|\\|\{|\}|\|)/g, '\\$1')
      this.hidden = !new RegExp(parsedQuery, 'i').test(this.searchLabel)
    }
  },

  mounted () {
    this.searchLabel = this.$el.innerHTML
    this.dispatch('XSelect', 'append')

    // 监听下拉选择框的关闭
    this.$on('close-select', () => {
      this.isFocus = false
    })
    this.$on('change-query', (val) => {
      this.queryChange(val)
    })
  },

  beforeDestroy () {
    this.dispatch('XSelect', 'remove')
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'
@import '../../styles/mixins/select'

select-item('x-select', 'x-select-item')

for theme in themes
  .theme-{theme}
    select-item-color(theme, 'x-select', 'x-select-item')

    .x-select--multiple .x-select-item
      &--selected
        color lookup(theme + '-select-multiple-item-selected-color')
        background lookup(theme + '-select-multiple-item-selected-bg')
      &--focus, &:hover
        background lookup(theme + '-select-multiple-item-hover-bg')
      &--selected:after
        color lookup(theme + '-select-multiple-item-selected-icon-color')

.x-select--multiple .x-select-item
  &--selected:after
    display inline-block
    font-family icons-font-family
    speak none
    font-style normal
    font-weight normal
    font-variant normal
    text-transform none
    text-rendering auto
    line-height 1
    -webkit-font-smoothing antialiased
    -moz-osx-font-smoothing grayscale
    float right
    font-size font-size-base
    content '\F00C'
</style>



